'use client';

import { useState, useEffect, useRef } from "react";
import { MoreVertical } from "lucide-react";
import { END_POINT } from "@/config";
import { X } from "lucide-react";


export default function ProductsPage() {

  const [catNames, setCatNames] = useState([]);
  const [products, setProducts] = useState([]);
  const [formData, setFromData] = useState({});
  const [showModal, setShowModal] = useState([]);
  
  const getAllProducts = async () => {
    await fetch(END_POINT.PRODUCTS, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'include',
    })
      .then(res => res.json())
      .then(json => {
        console.log(json.data)
        setProducts(json.data);
        const cats = json.data.map(e => e.name);
        const uniqueCats = new Set(cats);
        setCatNames(uniqueCats);
      })
      .catch(error => console.log(error.message));
  };

  const handleChange = () => {

  };

  const handleFileChange = () => {

  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const body = new FormData();

    body.append('desc', formData.desc);
    body.append('size', formData.size);
    body.append('title', formData.title);
    body.append('price', formData.price);
    body.append('stock', formData.stock);
    body.append('colors', formData.colors);
    body.append('images', formData.images);
    body.append('salePrice', formData.price);
    body.append('category', formData.category);

    fetch(END_POINT.PRODUCTS, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body,
      credentials: 'include',
    })
      .then(res => res.json())
      .then(json => console.log(json))
      .catch(error => console.log(error.message));
  };

  useEffect(() => {
    getAllProducts();
  }, []);

  return (
    <div className="p-8 flex-1">

      {/* PAGE HEADER */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-10">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Products</h1>
          <p className="text-gray-500 mt-1">Manage all products available in Threadix store.</p>
        </div>

        <button
          onClick={() => setShowModal(true)}
          className="px-5 py-2.5 bg-black text-white font-medium rounded-xl hover:bg-gray-900 transition"
        >
          + Add Product
        </button>
      </div>

      {/* SEARCH + FILTERS */}
      <div className="bg-white p-5 rounded-xl border shadow-sm mb-8 flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <input
          type="text"
          placeholder="Search products..."
          className="w-full md:w-72 px-4 py-2.5 border rounded-xl focus:ring-2 focus:ring-black outline-none transition"
        />

        <div className="flex gap-3">
          <select className="px-4 py-2.5 border rounded-xl text-gray-700 focus:ring-2 focus:ring-black outline-none">
            <option value="">All Categories</option>
            {catNames && catNames.length > 0 && catNames.map(cat => (
              <option value={cat}>{ cat }</option>
            ))}
          </select>

          <select className="px-4 py-2.5 border rounded-xl text-gray-700 focus:ring-2 focus:ring-black outline-none">
            <option value="">All Status</option>
            <option>Active</option>
            <option>Out of Stock</option>
          </select>
        </div>
      </div>

      {/* PRODUCTS TABLE */}
      <div className="bg-white border rounded-xl shadow-sm overflow-hidden">
        <table className="w-full min-w-[700px]">
          <thead className="bg-gray-50 border-b">
            <tr>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Product</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Category</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Price</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Status</th>
              <th className="text-right px-6 py-3 text-sm font-medium text-gray-600">Actions</th>
            </tr>
          </thead>

          <tbody>
            {products && products.length > 0 && products.map((product) => (
              <tr key={product.id} className="border-b last:border-none hover:bg-gray-50 transition">
                <td className="px-6 py-4 font-semibold text-gray-900">{product.title}</td>
                <td className="px-6 py-4 text-gray-700">{product.category}</td>
                <td className="px-6 py-4 text-gray-700">{product.price}</td>
                <td className="px-6 py-4">
                  <span
                    className={`px-2.5 py-1 text-xs font-medium rounded-lg ${
                      product.stock
                        ? "bg-green-100 text-green-700"
                        : "bg-red-100 text-red-700"
                    }`}
                  >
                    {product.status}
                  </span>
                </td>
                <td className="px-6 py-4 text-right">
                  <button className="p-2 hover:bg-gray-200 rounded-full transition">
                    <MoreVertical size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      {showModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/70 backdrop-blur-sm">
          <div className="w-full max-w-2xl bg-white rounded-2xl shadow-xl p-8">
            
            {/* Header */}
            <div className="flex items-center justify-between mb-6">
              <div>
                <h2 className="text-2xl font-bold">Add Product</h2>
                <p className="text-sm text-gray-500 mt-1">
                  Create a new product for Threadix
                </p>
              </div>
              <button
                onClick={() => setShowModal(false)}
                className="w-9 h-9 flex items-center justify-center rounded-full hover:bg-gray-100"
              >
                <X size={18} />
              </button>
            </div>

            {/* Form */}
            <form onSubmit={handleSubmit} className="space-y-6">

              {/* Product Name */}
              <div>
                <label className="block text-sm font-medium mb-2">
                  Product Name
                </label>
                <input
                  type="text"
                  name="title"
                  required
                  value={formData.title}
                  onChange={handleChange}
                  className="w-full px-4 py-3 rounded-xl border focus:ring-2 focus:ring-black outline-none"
                  placeholder="e.g. Oversized Hoodie"
                />
              </div>

              {/* Price */}
              <div>
                <label className="block text-sm font-medium mb-2">
                  Price
                </label>
                <input
                  type="number"
                  name="price"
                  required
                  value={formData.price}
                  onChange={handleChange}
                  className="w-full px-4 py-3 rounded-xl border focus:ring-2 focus:ring-black outline-none"
                  placeholder="e.g. 499"
                />
              </div>

              {/* Category */}
              <div>
                <label className="block text-sm font-medium mb-2">
                  Category
                </label>
                <select
                  name="category"
                  required
                  value={formData.category}
                  onChange={handleChange}
                  className="w-full px-4 py-3 rounded-xl border focus:ring-2 focus:ring-black outline-none"
                >
                  <option value="">Select category</option>
                  {catNames && catNames.length > 0 && catNames.map(cat => (
                    <option key={cat._id} value={cat._id}>
                      {cat.name}
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium mb-2">
                  Description
                </label>
                <textarea name="desc"></textarea>
              </div>

              {/* Image */}
              <div>
                <label className="block text-sm font-medium mb-2">
                  Product Image
                </label>
                <input
                  type="file"
                  name="image"
                  accept="image/*"
                  className="w-full"
                  onChange={handleFileChange}
                  multiple
                />
              </div>

              {/* Buttons */}
              <div className="flex justify-end gap-4 pt-4">
                <button
                  type="button"
                  onClick={() => setShowModal(false)}
                  className="px-6 py-3 rounded-xl border hover:bg-gray-100"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  // disabled={loading}
                  className="px-6 py-3 rounded-xl bg-black text-white font-semibold hover:bg-gray-900 disabled:opacity-50"
                >
                  {/* {loading ? "Adding..." : "Add Product"} */}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
